*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --font-family: "Roboto", sans-serif;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.button {
  color: #fff;
  font-size: inherit;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  border-radius: 4px;
  margin: 0 auto;
  padding: 12px 32px;
  display: flex;
}

.button:hover, .button:focus {
  background-color: #1976d2;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

span {
  color: #fff;
  font-weight: 600;
}

a {
  text-decoration: none;
}

.body-container {
  color: #2d2e30;
  width: 1280px;
  background: #fff;
  margin: 0 auto;
  font-family: Helvetica Neue, Roboto, Verdana, Tahoma, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
}

.nav-container {
  height: 216px;
  background-image: url("../header-background.28cae044.jpg");
  justify-content: center;
  align-items: center;
  display: flex;
}

.main-container {
  background: #fff;
  margin-top: 20px;
}

.cards-list {
  flex-wrap: wrap;
  gap: 32px 16px;
  margin: 60px 30px;
  display: flex;
}

.card {
  width: 395px;
  height: 634px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.card-img {
  width: 395px;
  height: 574px;
  background: url("../film500.80a0ab88.jpg");
  border-radius: 5px;
}

.card-text {
  font-family: var(--font-family);
  text-transform: uppercase;
  color: #000;
  font-size: 20px;
  font-weight: 500;
  line-height: 120%;
}

.text {
  color: #fff;
  padding: 20px 20px 40px;
  font-size: 18px;
}

.footer-container {
  background: #e0cece;
}

